import { require } from "../require"
/**
 * 获取最新 MV
 */
function mv(context, params = {}) {
    let url = "/mv/first";
    return require(url, params, context);
}

/**
 * 获取 MV url
 */
function mvUrl(context, params = {}) {
    let url = "/mv/url";
    return require(url, params, context);
}

/**
 * 获取相似 mv
 */
function similarMv(context, params = {}) {
    let url = "/simi/mv";
    return require(url, params, context);
}

/**
 * 获取 mv 信息
 */
function MvDetail(context, params = {}) {
    let url = "/mv/detail"
    return require(url, params, context);
}

/**
 * 获取 推荐 MV
 */
function IntroduceMv(context, params = {}) {
    let url = "/personalized/mv"
    return require(url, params, context);
}
export {
    mv,
    mvUrl,
    similarMv,
    MvDetail,
    IntroduceMv,
}