import { require } from "../require"
/**
 * 获取歌单评论
 */
function songComment(context, params = {}) {
    let url = "/comment/playlist";
    return require(url, params, context)
}

/**
 * 获取 mv 评论
 */
function mvComment(context, params ={}) {
    let url = "/comment/mv";
    return require(url, params, context);
}
export  {
    songComment,
    mvComment
}